import { useEffect } from "react";
import { cart } from "../../lib/services/cart/cart";
import { useRouter } from "next/router";

export default function NovAccountCartIntegration() {
    useEffect(() => {
        const urlSearchParams = new URLSearchParams(window.location.search);
        const params = Object.fromEntries(urlSearchParams.entries());        
        const checkoutId = params["checkout-id"] || "";
        let accountData: any = null;
        if (checkoutId) {
            accountData = (() => {
                try {
                    return JSON.parse(localStorage.getItem(`checkout-${checkoutId}.accountData`) || "null");
                }
                catch(err) {
                    return null;
                }    
            })();            
        }
        window.top.postMessage({
            type: "da-account-integration",
            accountData
        },"*");
    },[]);

    return "ok, let's try this thing";
}